@import 'utils/variables', 'utils/mixins';

.wrapper {
  width: 1082px;
  max-width: 100%;
  min-height: 100px;
  line-height: 24px;

  padding: 24px;
}

.header {
  font-weight: 700;
  display: flex;
  gap: 16px;
  margin-bottom: 24px;
}

.mainStatus {
  display: flex;
  gap: 10px;
  align-items: center;
}

.item {
  border: 1px solid $base-gray;
  border-radius: 4px;

  display: grid;
  align-items: center;
  grid-template-columns: auto 40px 64px 168px 168px 40px;
  gap: 28px;

  padding: 20px 16px;
  margin-bottom: 4px;
}

.col {
  display: flex;
  flex-direction: column;
  gap: 8px;

  &:nth-child(3) {
    align-items: center;
    text-align: center;
  }
}

.label {
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  color: $base-middle-gray;
}

.icon {
  fill: $base-middle-gray;
  cursor: pointer;

  &:hover {
    fill: $blue-2;
  }
}

.status {
  background: $base-dark-gray;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  margin: 2px 0;

  &.process {
    background: $yellow;
  }

  &.success {
    background: $green;
  }

  &.error {
    background: $red;
  }
}

.bottomBar {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.loader {
  display: flex;
  align-items: center;
  justify-content: center;

  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  background: rgba(0, 0, 0, 0.15);
}
